import React,{ Fragment } from 'react'

import './notifications.css'

import Manager from '../../components/containers/manager'
import Notif from '../../components/dumb/notif'
import MovieOrder from '../../modals/movie_order/MovieOrder'

class Notifications extends React.Component {
    state = {
        show_conversation_modal: false,
        notifications : [
            {
                title: 'Movie Order',
                quantity: 2,
                total: 100,
                from: {
                    name: 'Jane Doe',
                    imagePath: 'data:image/jpeg;base64,'
                }

            },
            {
                title: 'Movie Order',
                quantity: 2,
                total: 100,
                from: {
                    name: 'Jane Doe',
                    imagePath: 'logo192.png'
                }

            }
        ]
    }
    handleShowingConversationModal = ($show_bool)=>{
        if($show_bool===true){
            this.setState({
                ...this.state,
                show_conversation_modal: true
            })
        }
    }

    displayNotifications = (notifications)=>{
        if(notifications.length<1){
            return(
                <p>No Notifications</p>
            )
        }else {
            return(
                <Notif DATA={notifications}  SHOW_CONVERSATION_MANAGER_MODAL={this.handleShowingConversationModal}/>
            )
        }
    }

    CLOSEMODAL_FUNC = ()=>{
        this.setState({
            ...this.state,
            show_conversation_modal: false
        })
    }
    
    render(){
        let { notifications, show_conversation_modal } = this.state;

        return (
            <Fragment>
                <MovieOrder SHOW={show_conversation_modal} CLOSEMODAL_FUNC_PROP={this.CLOSEMODAL_FUNC} />
                <Manager TITLE="Notifications">
                    {this.displayNotifications(notifications)}
                </Manager>
            </Fragment>
            
        )
    }
}


export default Notifications;