import React, { Fragment } from 'react'

import './messageView.css'

import Modal from '../../components/containers/modal'
import YouMessage from '../../components/dumb/you_message'
import MeMessage from '../../components/dumb/me_message'

class MessageView extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            user: {
                name: 'James Mathwe',
                imagePath: 'data:image/jpeg;base64,',
                messages: [
                    {
                        from: 'Mathw',
                        text: 'Hello. Hi can I get a Movie after work. Already burned? ',
                        status: 'read'
                    },
                    {
                        to: 'Mathw',
                        text: 'Yes. Sure. Which one in particular.Can you send me a list',
                        status: 'read'
                    },
                    {
                        from: 'Mathw',
                        text: 'Hello. Hi can I get a Movie after work. Already burned?',
                        status: 'read'
                    },
                    {
                        from: 'Mathw',
                        text: 'Hello. Hi can I get a Movie after work. Already burned? ',
                        status: 'read'
                    },
                    {
                        to: 'Mathw',
                        text: 'Yes. Sure. Which one in particular.Can you send me a list',
                        status: 'read'
                    },
                    {
                        from: 'Mathw',
                        text: 'Hello. Hi can I get a Movie after work. Already burned?',
                        status: 'read'
                    },
                ]
            }
        }
    }
    
    renderUserMessageInfo =(user)=>{
        return (
            <div className="Message_userInfo">
                <img src={user.imagePath} alt="user_image"/>
                <div>{ user.name }</div>
                <div className="messages_modal_input_div">
                        <input type="text" placeholder="type message" /> 
                        <button>Send</button> 
                </div>
            </div>
        )
    }

    displayMessages = (user)=>{
        let messages = user.messages;
        return (
            <Fragment>
                {messages.map((message,index)=>(
                    

                    'from' in message===true?<div key={"text"+index}><YouMessage DATA={ message } /></div>:<div key={"text"+index}><MeMessage DATA={ message } /></div>
                )
                )}
            </Fragment>
        )
    }
    render(){
        let { user } = this.state;
        let { SHOW, CLOSEMODAL_FUNC_PROP } = this.props;
        return (
            <Modal TITLE={this.renderUserMessageInfo(user)} SHOW={SHOW} CLOSEMODAL_FUNC_PROP={CLOSEMODAL_FUNC_PROP}>
                <div className="messages_display_div">

                    {this.displayMessages(user)}
                   
                </div>
                
            </Modal>
        )
    }
}


export default MessageView;